const $ = require(131);
const CodeMirror = require(79);

// editing addons
require(60);
require(61);
require(62);
require(63);
require(64);
require(65);

// code hinting addons
require(67);
require(68);
require(69);
require(70);
require(71);

// code linting addons
require(77);
require(74); // requires htmlhint
require(73);
require(75);
require(76);

// code selection active line
require(78);

// code modes
require(82);
require(80);
require(81);

const debounce = require(5);

const $preview = $('#custom-verification-preview');
const $editor = $('#textarea-custom-verification-html');
const cm = CodeMirror.fromTextArea($editor.get(0), {
  theme: 'material',
  mode: 'htmlmixed',
  lineNumbers: true,
  lineWrapping: true,
  spellcheck: true,
  autocorrect: true,
  showTrailingSpace: true,
  styleActiveLine: true,
  continueComments: true,
  autoRefresh: true,
  readOnly: window.IS_CUSTOM_VERIFICATION_DISABLED === true,
  lint: true,
  gutters: ['CodeMirror-lint-markers']
});

function change() {
  const val = cm.getValue();
  $preview.contents().find('html').html(val);
}

change();

cm.on('change', debounce(change, 300));
